export const ObjectsGridColums = (props) => {
   
    return [
        {
            key: 'id_obj', 
            name: '`', 
            editable: false, 
            width: 100,
            renderCell: (props) => {
                return <div>{props.row.id}</div>;
            }
        }
    ];
} 

